html {
	box-sizing: border-box;
	height: 100%;
	background-color: #fafafa;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
::selection {
	background: pink;
}
a {
	color: white;
	font-family: 'Roboto', sans-serif;
	font-variant: none;
	text-decoration: none;
}
body {
	margin: 0;
	padding: 0;
	border: 0;
}
body.m-locked {
	overflow: hidden;
}
body:before {
	display: block;
	width: 100%;
	height: 100%;
}
p,
h6 {
	font-family: 'Roboto', sans-serif;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
figure,
h1,
h4 {
	font-family: 'Roboto Mono', monospace;;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
h4.title {
  letter-spacing: 0.7;
  font-weight: 400;
  text-align: left;
  letter-spacing: 0.7px;
  color: black;
  padding-top: 175px;
  margin-bottom: 0;
}

h1.m-textSmall, h1.m-textLarge {
	font-family: 'Futura',  monospace;
	color: black;
	font-weight: 700;
}

h1.m-textSmall {
font-family: 'Futura', Arial, sans-serif;
}

h2,
h3,
h5 {
	font-family: 'Roboto Mono', monospace;
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
}
h1 {
	text-align: left;
	font-weight: 300;
	line-height: 1.3;
	color: $darkGreyText;
}
h1.brandDescription {
	font-weight: 300;
}
h2 {
	font-size: $fontUp;
	font-weight: 300;
	line-height: 36px;
}
h3 {
	font-size: $fontUp;
	font-weight: 400;
	line-height: 24px;
}
h4 {
	font-size: $fontDown;
	font-weight: 400;
	letter-spacing: 0.1px;
	color: $darkGreyText;
}
h5 {
	display: inline-block;
	font-size: $fontDown;
	letter-spacing: 0.7px;
	font-weight: 500;
	padding-top: 40px;
	padding-right: 20px;
	text-transform: uppercase;
	color: white;
}
p {
	font-size: $fontBase;
	font-weight: 400;
	letter-spacing: 0.2px;
	color: #525252;
	line-height: 1.7;
}
h4.m-projTitle {
	letter-spacing: 0.7;
	font-weight: 500;
	text-align: left;
	letter-spacing: 0.7px;
	color: #333333;
}
.description {
	font-size: $fontDown;
	padding: 0 0 5px;
	text-align: left;
	color: $darkGreyText;
}
li {
	font-size: $fontDown;
	font-weight: 400;
	line-height: 24px;
	padding: 0;
	margin: 0;
	list-style: none;
	letter-spacing: 0;
}
